export class Queue {
  constructor() {
    this.count = 0; // éåé¿åº¦
    this.lowestCount = 0; // ç¬¬ä¸ä¸ªåç´ 
    this.items = {};
  }

  // åéåå°¾é¨æ·»å ä¸ä¸ªï¼æå¤ä¸ªï¼æ°çé¡¹
  enqueue = (element) => {
    this.items[this.count] = element;
    this.count++;
  }

  // ç§»é¤éåçç¬¬ä¸é¡¹ï¼å³æå¨éåæåé¢çé¡¹ï¼å¹¶è¿åè¢«ç§»é¤çåç´ 
  dequeue = () => {
    if (this.isEmpty()) {
      return undefined;
    }

    const result = this.items[this.lowestCount];
    delete this.items[this.lowestCount];
    this.lowestCount++;
    return result;
  }

  // è¿åéåä¸­ç¬¬ä¸ä¸ªåç´ ââæåè¢«æ·»å ï¼ä¹å°æ¯æåè¢«ç§»é¤çåç´ 
  // éåä¸åä»»ä½åå¨ï¼ä¸ç§»é¤åç´ ï¼åªè¿ååç´ ä¿¡æ¯ââä¸Stackç±»çpeekæ¹æ³éå¸¸ç±»ä¼¼ï¼
  // è¯¥æ¹æ³å¨å¶ä»è¯­è¨ä¸­ä¹å¯ä»¥å«ä½frontæ¹æ³
  peek = () => {
    if (this.isEmpty()) {
      return undefined;
    }

    return this.items[this.lowestCount];
  }

  isEmpty = () => {
    return this.count - this.lowestCount === 0;
  }

  size = () => {
    return this.count - this.lowestCount;
  }

  toString() {
    if (this.isEmpty()) {
      return '';
    }
    let objString = `${this.items[this.lowestCount]}`;
    for (let i = this.lowestCount + 1; i < this.count; i++) {
      objString = `${objString},${this.items[i]}`;
    }
    return objString;
  }

}

export class Deque {
  constructor() {
    this.count = 0;
    this.lowestCount = 0;
    this.items = {};
  }
}
