import dependenceManager from "./s-dependence-manager";

let obIDCounter = 1;

class Observable {
  /**
   * å¨å±å¯ä¸ id
   * @type {number}
   */
  obID = 0;

  /**
   * çå®å¼
   * @type {null}
   */
  value = null;

  constructor(v) {
    this.obID = "ob-" + ++obIDCounter;

    if (Array.isArray(v)) {
      this._wrapArrayProxy(v);
    } else {
      this.value = v;
    }
  }

  get() {
    // æ·»å ä¾èµ
    dependenceManager.collect(this.obID);
    return this.value;
  }

  set(v) {
    if (Array.isArray(v)) {
      this._wrapArrayProxy(v);
    } else {
      this.value = v;
    }
    // è§¦åä¾èµ
    dependenceManager.trigger(this.obID);
  }

  /**
   * æå¨è§¦åä¾èµ
   */
  trigger() {
    dependenceManager.trigger(this.obID);
  }

  /**
   * å¯¹æ°ç»åè£Proxyæ¦æªæ°ç»æä½çå¨ä½
   */
  _wrapArrayProxy(v) {
    this.value = new Proxy(v, {
      set: (obj, key, value) => {
        obj[key] = value;
        if (key != "length") {
          this.trigger();
        }
        return true;
      },
    });
  }
}

export default Observable;
